<template>
  <div class="main">
    <van-tabbar v-model="index" active-color="slateblue" inactive-color="#888" @change="goPage">
      <van-tabbar-item v-for="(item,index) in msg" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item>
    </van-tabbar>
    <!-- 二级路由插槽 -->
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      msg: [
        {
          icon: "wap-nav",
          title: "商品",
          name: "Menu",
          url:"/main/menu"
        },
        {
          icon: "column",
          title: "订单",
          name: "Order",
          url:"/main/order"
        },
        {
          icon: "cart",
          title: "购物车",
          name: "Shopcart",
          url:"/main/shopcart"
        },
        {
          icon: "manager",
          title: "我的",
          name: "My",
          url:"/main/my"
        }
      ]
    };
  },
  created() {
    //获取路径的hash
    let hash = location.hash.slice(1);
    // 
    // 激活对应tab
    for (let i = 0; i < this.msg.length; i++) {
      if (this.msg[i].url == hash) {
        this.index = i;
        break;
      }
    }
  },
  methods: {
    // 跳转页面
    goPage(index) {
      // 
      this.$router.push({ name: this.msg[index].name });
    }
  }
};
</script>

<style lang="less" scoped>
</style>